const formRules = {
  requiredRule: { required: true, message: ' ' },
  passMatch: ({ getFieldValue }: any) => ({
    validator(_: any, value: string) {
      if (!value || getFieldValue('password') === value) {
        return Promise.resolve();
      }
      return Promise.reject(new Error('  !'));
    },
  }),
  emailRule: () => ({ type: 'email', message: ' email' }),
  min: (value: number) => ({ min: value, message: ` - : ${value}` }),
  max: (value: number) => ({ max: value, message: ` - : ${value}` }),
};

export { formRules };
