import React, { useMemo } from 'react';
import { Button, Form, Input, message } from 'antd';
import { observer } from 'mobx-react';
import { StyledForm, StyledFormItem } from './styles';
import { formRules } from '../../utils/consts';
import authStore from '../../stores/AuthStore';

const SignInForm = observer(() => {
  const [form] = Form.useForm();

  const { requiredRule, passMatch } = formRules;
  const { signInUser, setUser, signInUserLoadingStatus } = authStore;

  const isLoading = useMemo(() => signInUserLoadingStatus === 'loading', [signInUserLoadingStatus]);

  const handleSubmit = async (values: any) => {
    const { email, password } = values;

    try {
      const user = await signInUser({ email, password });
      setUser(user);
      message.success('.');
    } catch (e: unknown) {
      message.error('     .');
    }
  };

  return (
    <StyledForm
      onFinish={handleSubmit}
      form={form}
      layout='vertical'
      variant='filled'
      disabled={isLoading}
    >
      <StyledFormItem
        name='email'
        label='Email'
        rules={[requiredRule, { type: 'email', message: ' email' }]}
      >
        <Input size='small' />
      </StyledFormItem>
      <StyledFormItem name='password' label='' rules={[requiredRule]}>
        <Input.Password size='small' type='password' />
      </StyledFormItem>
      <StyledFormItem
        name='repeatPassword'
        label=' '
        dependencies={['password']}
        rules={[requiredRule, passMatch]}
      >
        <Input.Password size='small' type='password' />
      </StyledFormItem>
      <Button loading={isLoading} htmlType='submit'>
        
      </Button>
    </StyledForm>
  );
});

export default SignInForm;
